<template>
  <div class="file-list">
    <h1>
      <a href="https://xiaodongxier.com" target="_blank">
        <a-tooltip placement="left">
          <template slot="title">
            王永杰的<a href="https://github.com/xiaodongxier/mincode" target="_blank">MinCode</a>仓库列表
          </template>
          <a-icon type="github" />
        </a-tooltip>
      </a>
    </h1>
    <a-row :gutter="10">
      <a-col :span="8" class="layout_item layout_list">
        <div>
          <a-table :columns="columns" :data-source="files" :pagination="false" :scroll="scroll" :loading="tableLoading"
            row-key="path">
            <span slot="name" slot-scope="text, record">
              {{ record.name.split('.')[0] }}
            </span>

            <span slot="type" slot-scope="text, record">
              <span v-if="fileTagFile(record.path) === 'vue'">
                <a-tag color="green">Vue</a-tag>
              </span>
              <span v-if="fileTagFile(record.path) === 'html'">
                <a-tag color="pink">HTML</a-tag>
              </span>
              <span v-if="fileTagFile(record.path) === 'css'">
                <a-tag color="blue">CSS</a-tag>
              </span>
              <span v-if="fileTagFile(record.path) === 'md'">
                <a-tag color="cyan">MD</a-tag>
              </span>
              <span v-if="fileTagFile(record.path) === 'image'">
                <a-tag color="orange">图片</a-tag>
              </span>
              <span v-if="fileTagFile(record.path) === 'other'">
                <a-tag>未知类型</a-tag>
              </span>
            </span>
            <span slot="action" slot-scope="text, record">
              <span v-if="0">{{ text }}{{ record }}</span>
              <a @click="previewFile(record)">源码</a>
              <a-divider type="vertical" />
              <a :href="link(record.name)" target="_blank">打开</a>
            </span>
          </a-table>
        </div>
      </a-col>
      <a-col :span="16" :class="['layout_item', 'layout_content', previewType !== 'code' ? 'preview' : '']">
        <div>
          <a-spin :spinning="loading">
            <pre v-if="previewType === 'code'">{{ pageContent }}</pre>
            <img v-else-if="previewType === 'preview'" :src="pageContent" alt="">
            <div v-else-if="previewType === 'other'">其他格式</div>
            <div v-else style="font-size: 50px;color: #e9e9e9;">预览区域</div>
          </a-spin>
        </div>
      </a-col>
    </a-row>

  </div>
</template>

<script>
export default {
  data() {
    return {
      title: '文件列表',
      scroll: { y: "calc(100vh - 30px)" },
      tableLoading: false,
      columns: [
        {
          title: '序号',
          dataIndex: 'order',
          key: 'order',
          align: 'center',
          width: 70,
          customRender: function (t, r, index) {
            return parseInt(index) + 1;
          }
        },
        {
          title: '名称',
          dataIndex: 'name',
          key: 'name',
          align: 'center',
          scopedSlots: { customRender: 'name' },
        },
        {
          title: '类型',
          dataIndex: 'type',
          key: 'type',
          align: 'center',
          width: 70,
          scopedSlots: { customRender: 'type' },
        },
        {
          title: '操作',
          dataIndex: 'Action',
          key: 'action',
          width: 130,
          scopedSlots: { customRender: 'action' },
          align: 'center',
        }
      ],
      files: [],
      pageContent: '',
      loading: false,
      previewType: '',
      ghp_rq1ipDLBSsSCZZsBEj1JMhAY2Nwilm0L1EXE: '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'
    }
  },
  mounted() {

  },
  created() {
    this.getdata()
  },
  watch: {
    // pageContent: {
    //   handler(newVal, oldVal) {

    //   },
    //   immediate: true,
    //   deep: false
    // },
  },
  methods: {
    getdata() {
      const baseUrl = 'https://api.github.com/repos/xiaodongxier/mincode/contents/';
      const ghp_rq80pDLSlSC56ZSBEj4MhlAY2Nwilm0LMEXsE = this.ghp_rq1ipDLBSsSCZZsBEj1JMhAY2Nwilm0L1EXE;
      this.tableLoading = true
      this.$http.get(baseUrl)
        .then(response => {
          if (response.data) {
            this.tableLoading = false
            let newdata = response.data.filter(item => item.type !== 'dir' && (item.name).charAt(0) !== '.' && item.name !== 'README.md' && item.name !== 'index.html');
            this.files = newdata
            this.openNotificationWithIcon('success', '仓库列表加载成功');
          } else {
            this.openNotificationWithIcon('warning', '加载失败,重新获取中...');
            this.$http.get(baseUrl, {
              headers: {
                Authorization: ghp_rq80pDLSlSC56ZSBEj4MhlAY2Nwilm0LMEXsE
              },
            })
              .then(response => {
                this.$http.get('https://api2.pushdeer.com/message/push?pushkey=PDU22927TRAsfvL9YIyyKBq98BadSChARaHP2aH5P&text=git仓库列表收费key使用中...')
                this.tableLoading = false
                let newdata = response.data.filter(item => item.type !== 'dir' && (item.name).charAt(0) !== '.' && item.name !== 'README.md' && item.name !== 'index.html');
                this.files = newdata;
                // this.openNotification();
                this.openNotificationWithIcon('success', '仓库列表加载成功');
              })
          }

        })
    },
    link(href) {
      return 'https://xiaodongxier.github.io/mincode/' + href
    },
    previewFile(itemData) {
      this.loading = true
      this.pageContent = ''
      if (itemData.type === 'file') {
        // 1. 源码显示 例如：html,vue,js...
        // 2. 直接预览 例如：img,image,png...
        // 3. 直接不展示 例如：zip,rar...
        if (this.fileType(itemData.path) === 'SourceCode') {
          this.previewType = 'code'
          this.$http.get(itemData.download_url)
            .then(response => {
              this.pageContent = response.data
              this.loading = false
            })
        } else if (this.fileType(itemData.path) === 'PreviewView') {
          this.previewType = 'preview'
          this.pageContent = itemData.download_url
          this.loading = false
        } else {
          this.previewType = 'other'
          this.loading = false
        }

      } else {
        this.$success
        this.loading = false
      }

    },
    fileType(filePath) {
      const fileType = filePath.split('.')[1];
      switch (fileType) {
        case 'html':
        case 'htm':
        case 'css':
        case 'js':
        case 'json':
        case 'md':
        case 'txt':
        case 'xml':
        case 'vue':
        case 'jsx':
          return 'SourceCode';
        case 'image':
        case 'img':
        case 'png':
        case 'pdf':
          return 'PreviewView';
        default:
          return 'other';
      }
    },
    fileTagFile(filePath) {
      const fileType = filePath.split('.')[1];
      switch (fileType) {
        case 'html':
          return 'html'
        case 'css':
          return 'css'
        case 'js':
          return 'js'
        case 'json':
          return 'json'
        case 'md':
          return 'md'
        case 'txt':
          return 'txt'
        case 'vue':
          return 'vue'
        case 'jsx':
          return 'jsx';
        case 'image':
        case 'img':
        case 'png':
          return 'image';
        default:
          return 'other';
      }
    },
    onIframeLoad() {
    },
    openNotification() {
      this.$notification.open({
        message: '提示',
        description:
          '仓库列表加载成功',
        icon: <a-icon type="smile" style="color: #108ee9" />,
      });
    },
    openNotificationWithIcon(type, info) {
      this.$notification[type]({
        message: '提示',
        description: info,
        // placement: 'bottomRight'
      });
    },
  }
}
</script>

